// -----------------------------------------------------------------------------
// This file contains all styles related to the hero component.
// -----------------------------------------------------------------------------

/**
* Hero Component
**/
.hero {
	position: relative;
	height: 300px;
	background-image: url("../assets/images/bank-tree.webp");
	background-position: 0 -50px;
	background-repeat: no-repeat;
	background-size: cover;

	@media (min-width: 920px) {
		height: 400px;
		background-position: 0% 33%;
	}

	&-content {
		position: relative;
		top: 2rem;
		width: 200px;
		margin: 0 auto;
		padding: 2rem;
		background: white;
		text-align: left;

		@media (min-width: 920px) {
			position: absolute;
			top: 50px;
			right: 50px;
			width: 300px;
			margin: 2rem;
		}

		.subtitle {
			margin: 0;
			font-size: 1rem;
			font-weight: bold;

			@media (min-width: 920px) {
				font-size: 1.5rem;
			}
		}

		.text {
			margin-bottom: 0;
			font-size: 0.9rem;

			@media (min-width: 920px) {
				font-size: 1.2rem;
			}
		}
	}
}