// -----------------------------------------------------------------------------
// This file contains all styles related to the profile page.
// -----------------------------------------------------------------------------

/**
* Profile page
*/
.account {
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	width: 80%;
	margin: 0 auto;
	margin-bottom: 2rem;
	padding: 1.5rem;
	border: 1px solid black;
	background-color: #fff;
	text-align: left;

	@media (min-width: 720px) {
		flex-direction: row;
	}

	&-amount {
		margin: 0;
		font-size: 2.5rem;
		font-weight: bold;

		&-description {
			margin: 0;
		}
	}

	&-title {
		margin: 0;
		padding: 0;
		font-size: 1rem;
		font-weight: normal;
	}

	&-content {

		&-wrapper {
			flex: 1;
			width: 100%;

			&.cta {
				flex: 0;
			}
		}
	}
}

.header {
	color: #fff;
	margin-bottom: 2rem;
}

.edit-button {
    border-color: #00bc77;
    background-color: #00bc77;
    color: #fff;
    font-weight: bold;
    padding: 10px;
	cursor: pointer;
}

.transaction-button {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 1rem;
    border-color: #00bc77;
    background-color: #00bc77;
    color: #fff;
	cursor: pointer;

	@media (min-width: 720px) {
		width: 200px;
	}
}