// -----------------------------------------------------------------------------
// This file contains all styles related to the form component.
// -----------------------------------------------------------------------------

/**
* Form Component
**/
.sign-in {
    &-content {
		box-sizing: border-box;
		width: 300px;
		margin: 0 auto;
		margin-top: 3rem;
		margin-bottom: 3rem;
		padding: 2rem;
		background-color: white;
	}

    &-icon {
		font-size: 5rem;
	}

	&-button {
		display: block;
		width: 100%;
		margin-top: 1rem;
		padding: 8px;
		border-color: #00bc77;
		background-color: #00bc77;
		color: #fff;
		font-size: 1.1rem;
		font-weight: bold;
	}
}

.sign-in-content {
	form {
		.input {
			&-remember {
				display: flex;

				label {
					margin-left: 0.25rem;
				}
			}

			&-error {
				margin-top: 15px;
				color: red;
			}

			&-wrapper {
				display: flex;
				flex-direction: column;
				margin-bottom: 1rem;
				text-align: left;

				label {
					font-weight: bold;
				}

				input {
					padding: 5px;
					font-size: 1.2rem;
				}
			}
		}

		.error-message { 
            color: red;
            font-weight: bold;
            font-size: 1.1rem;
            margin-top: 25px;
        }
	}
}